.todo-container {
  border: 1px solid rgb(204, 203, 203);
  padding: 20px;
}
.add-todo {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 1px solid rgb(128, 126, 126);
  font-size: 20px;
}
#input_new-todo {
  width: 100%;
  height: 40px;
}

.todo-name {
  color: #646262;
}
.item-checkbox {
  -webkit-transform: scale(2);
          transform: scale(2);
}
.add-item {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  font-size: 19px;
}

.todo-done {
  text-decoration: line-through;
  opacity: 0.5;
}
.todo-not-done {
  text-decoration: none;
}
